import { hot, getSong, getMusicDetail, getMusicSong } from "@/api/hot/index.js";
import { Toast } from "vant";
export default {
  name: "Music",
  components: {},

  data() {
    return {
      song: [],
      songObj: {}
    };
  },

  computed: {},

  async created() {
    if (this.song.length <= 0) {
      this.song = JSON.parse(localStorage.getItem("song"));
    } // console.log(this.$route);


    if (this.$route.query.id) {
      var {
        songs
      } = await getMusicSong({
        id: this.$route.query.id,
        limit: 100
      });
    } else {
      var {
        songs
      } = await getMusicSong();
    }

    this.song = songs;
    localStorage.setItem("song", JSON.stringify(this.song));
  },

  methods: {
    goHome() {
      this.$router.push({
        path: "/home"
      });
    },

    musicChange(index) {
      console.log(index);
      this.songClick(this.song[index]);
    },

    async songClick(item) {
      console.log(item);
      let {
        data
      } = await getSong({
        id: item.id
      });
      console.log(data);
      this.songObj = data[0];

      if (!this.songObj.url) {
        Toast({
          message: "暂无资源",
          icon: "warning"
        });
      }

      console.log(this.songObj);
    }

  }
};