import request from "../request"

export function hot(){
    return request({
        url: "/personalized/newsong",
        method: "GET",
      });
}

export function getSong(data){
    return request({
        url: `/song/url/id`,
        method: "GET",
        data
      });
}

export function getBanner(data = {type:2}){
  return request({
      url: `/banner`,
      method: "GET",
      data
    });
}

// /

export function getMusic(data = {limit:100}){
  return request({
      url: `/personalized`,
      method: "GET",
      data
    });
}


// 

export function getMusicDetail(id = 156934569){
  return request({
      url: `/playlist/detail?id=${id}`,
      method: "GET"
    });
}

export function getMusicSong(data = {id:156934569,limit:100}){
  return request({
      url: `/playlist/track/all`,
      method: "GET",
      data
    });
}

export function getDj(data = {limit:30}){
  return request({
      url: `/dj/personalize/recommend`,
      method: "GET",
      data
    });
}
